.App {
  text-align: center;
  background-color: #F5D5AE;
  width: 50vw;
  margin: auto;
}

.button {
  width: 8vw;
  height: 5vw;
  border: 1px solid black;
  margin: 2%;
  font-size: 2rem;
  align-items: center;
  text-align: center;
  background-color:#EF9A53;
}

.input-box {
  display: block;
  margin: auto;
  width: 37vw;
  height: 5vw;
  font-size: 2rem;
  text-align: right;
}

.button-container{
  width: 40vw;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1vw;
}